import gql from 'graphql-tag'

const LIST_GUIDES_LOCAL = gql`query getGuideLocal($after: String, $createdGte: DateTime, $createdLte: DateTime, $guideNumber: String, $status: String, $name: String){
    guidesLocal(after: $after, created_Date_Gte: $createdGte, created_Date_Lte: $createdLte, guideNumber_Icontains: $guideNumber, status_Iexact: $status, name_Icontains: $name){
        pageInfo{
            hasNextPage
            endCursor
            startCursor
        }
        edges{
            cursor
            node{
                id
                address
                phoneNumber
                city
                name
                customerAnnotations
                guideNumber
                store{
                    name
                    email
                    phoneNumber
                }
                userCreated{
                    firstName
                    lastName
                }
                status
                created
                guidelocalproductthroughSet{
                    edges{
                        node{
                            quantity
                            product{
                                name
                            }
                            price
                            productObservations
                        }
                    }
                }
            }
        }
    }
}`

const GET_GUIDE = gql`query getGuideLocal($after: String, $createdGte: DateTime, $createdLte: DateTime, $guideNumber: String, $status: String, $name: String){
    guidesLocal(after: $after, created_Date_Gte: $createdGte, created_Date_Lte: $createdLte, guideNumber_Icontains: $guideNumber, status_Iexact: $status, name_Icontains: $name){
        pageInfo{
            hasNextPage
        }
        edges{
            cursor
            node{
                guideNumber
                guidelocalproductthroughSet{
                    edges{
                        node{
                            quantity
                            product{
                                name
                                sku
                                id
                            }
                            price
                            productObservations
                        }
                    }
                }
            }
        }
    }
}`

