import gql from 'graphql-tag'

const CREATE_GUIDE_LOCAL = gql`mutation createGuideLocal($guideLocal: GuideLocalCreateInput!){
    createGuideLocal(guideLocal: $guideLocal){
        guideLocal{
            guideNumber
            store{
                phoneNumber
                email
                name
            }
            address
            city
            phoneNumber
            name
            customerAnnotations
            guidelocalproductthroughSet{
                edges{
                    node{
                        price
                        product{
                            name
                        }
                        quantity
                        productObservations
                    }
                }
            }
        }
        success
    }
}`

const REVOKE_GUIDE = gql`mutation cancelGuide($guideNumber: String!){
    revokeGuide(guideLocal: $guideNumber){
        success
    }
}`

const MARK_PRINTED_GUIDE = gql`mutation markGuidePrinted($guides: [String!]!){
    markPrintedGuide(guides: $guides){
        success
    }
}`

const DELIVERY_ADDRESS_GUIDE = gql`mutation DeliverAddressGuideLocal($guides: [String!]!, $status: String!, $deliveryObservations: String){
    deliverAddressGuide(guides: $guides, status: $status, deliveryObservations: $deliveryObservations){
        success
    }
}`

export {
    CREATE_GUIDE_LOCAL,
    REVOKE_GUIDE,
    MARK_PRINTED_GUIDE,
    DELIVERY_ADDRESS_GUIDE
}