<template>
  <layout>
    <page-header :title="title"></page-header>
    <b-card>
      <b-card-text>
        <b-row>
          <b-col md="4">
            <b-form-group class="mb-2">
              <b-form-datepicker
                v-model="startDate"
                placeholder="Fecha Inicial"
                :max="maxDate"
              >
              </b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="4">
            <b-form-group class="mb-2">
              <b-form-datepicker
                v-model="endDate"
                placeholder="Fecha Final"
                :max="maxDate"
                :min="minEndDate"
              >
              </b-form-datepicker>
            </b-form-group>
          </b-col>
          <b-col md="1">
            <b-button
              :disabled="startDate !== null && endDate !== null ? false : true"
              @click="getGuidesLocal(true)"
              variant="success"
            >
              Enviar
            </b-button>
          </b-col>
          <b-col md="1">
            <b-button
              v-show="startDate && endDate"
              @click="clearDates()"
              variant="info"
            >
              Limpiar
            </b-button>
          </b-col>
        </b-row>
        <b-row>
          <b-col lg="4" md="4">
            <b-form @submit.prevent class="mb-3">
              <b-form-group>
                <b-input-group>
                  <b-form-input
                    id="input-1"
                    v-model="searchGuide"
                    type="text"
                    placeholder="Buscar por número de guía"
                    required
                  ></b-form-input>
                  <b-input-group-append>
                    <b-button
                      type="submit"
                      :disabled="!searchGuide"
                      @click="getGuidesLocal(true)"
                      variant="warning"
                      >Buscar</b-button
                    >
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-form>
          </b-col>
          <b-col lg="4" md="4">
            <b-form @submit.prevent class="mb-3">
              <b-form-group>
                <b-input-group>
                  <b-form-input
                    v-model="nameDestinatario"
                    type="text"
                    placeholder="Buscar por destinatario"
                    required
                  ></b-form-input>
                  <b-input-group-append>
                    <b-button
                      type="submit"
                      :disabled="!nameDestinatario"
                      @click="getGuidesLocal(true)"
                      variant="warning"
                      >Buscar</b-button
                    >
                  </b-input-group-append>
                </b-input-group>
              </b-form-group>
            </b-form>
          </b-col>
        </b-row>
        <template v-if="loadingGuides">
          <b-skeleton-table
            :rows="10"
            :columns="3"
            :table-props="{ bordered: true, striped: true }"
          ></b-skeleton-table>
        </template>
        <template v-else>
          <div class="table-responsive">
            <b-table
              striped
              hover
              :items="guidesTable"
              :fields="fields"
              responsive="sm"
              stacked="sm"
            >
              <template v-slot:cell(actions)="row">
                <b-button
                  @click="row.toggleDetails"
                  variant="primary"
                  size="sm"
                >
                  <i class="mdi mdi-eye"></i>
                  {{ row.detailsShowing ? "Ocultar" : "Ver" }} Detalles
                </b-button>
              </template>
              <template #row-details="row">
                <b-card tag="article" class="mb-2">
                  <p><strong>Destinatario: </strong>{{ row.item.name }}</p>
                  <p><strong>Celular: </strong>{{ row.item.phoneNumber }}</p>
                  <p><strong>Dirección: </strong>{{ row.item.address }}</p>
                  <p>
                    <strong>Productos: </strong
                    ><span v-html="row.item.products"></span>
                  </p>
                  <p><strong>Valor: </strong>{{ row.item.value }}</p>
                  <div 
                    class="text-center"
                    v-if="can('add_delivery')"
                  >
                    <b-button
                      variant="info"
                      v-b-modal.modal-lg
                      @click="guideLocalSelect = row.item"
                    >
                      Finalizar
                    </b-button>
                  </div>
                </b-card>
              </template>
            </b-table>
          </div>
        </template>
      </b-card-text>
      <div class="text-center mt-2" v-if="hasNextPage">
        <b-button
          block
          @click="getGuidesLocal()"
          variant="primary"
          :disabled="loadingMore"
        >
          <template v-if="loadingMore">
            <b-spinner small type="grow"></b-spinner>
            Cargando...
          </template>
          <template v-else> Ver más </template>
        </b-button>
      </div>
    </b-card>
    <b-modal
      v-if="guideLocalSelect"
      id="modal-lg"
      size="lg"
      :title="`Guía Número ${guideLocalSelect.guideNumber}`"
      :no-close-on-backdrop="true"
      :no-close-on-esc="true"
      :hide-footer="true"
      @close="onCloseModal"
    >
      <div class="mb-3">
        <b-form-group 
          label="Finaliza domicilio"
          invalid-feedback="Este campo es requerido"
        >
          <b-form-select
            v-model="formDelivery.selectedStatus"
            :options="statusGuide"
            :state="state($v.formDelivery.selectedStatus)"
            class="form-select"
            required
          ></b-form-select>
        </b-form-group>
        <b-form-group 
          label="Observaciones de entrega"
          invalid-feedback="Este campo es requerido"
        >
          <b-form-textarea
            id="textarea"
            v-model="formDelivery.deliveryObservations"
            placeholder="Observaciones de entrega"
            rows="3"
            max-rows="3"
          ></b-form-textarea>
        </b-form-group>
      </div>
      <div class="text-center">
        <b-button 
          variant="success"
          @click="validate"
          :disabled="loadingFinish"
        >
          <template v-if="loadingFinish">
            <b-spinner small></b-spinner>
            Cargando...
          </template>
          <template v-else>Finalizar</template>
        </b-button>
      </div>
    </b-modal>
  </layout>
</template>
<script>
import Layout from "@/router/layouts/main";
import PageHeader from "@/components/page-header";
import { LIST_GUIDES_LOCAL } from "@/api/graphql/query/guideLocal";
import { required } from "vuelidate/lib/validators";
import { DELIVERY_ADDRESS_GUIDE } from "@/api/graphql/mutations/guideLocal";
import { toast } from "@/utils/alerts";
import { formatPrice } from "@/utils/general";
import permissionMixin from '@/mixins/permissionMixin'

export default {
  name: "Domicilio",
  mixins: [
    permissionMixin
  ],
  components: {
    PageHeader,
    Layout,
  },
  data() {
    const now = new Date();
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    return {
      title: "Guías",
      loadingGuides: true,
      hasNextPage: false,
      loadingMore: false,
      fields: [
        {
          key: "guideNumber",
          label: "Nº Guía",
        },
        {
          key: "name",
          label: "Destinatario"
        },
        {
          key: "actions",
          label: "Acciones",
        },
      ],
      guidesTable: [],
      guidesDetail: [],
      guideLocalSelect: null,
      endCursor: null,
      searchGuide: null,
      startDate: null,
      endDate: null,
      maxDate: today,
      minEndDate: null,
      formDelivery: {
        selectedStatus: null,
        deliveryObservations: null,
      },
      statusGuide: [
        {
          value: null,
          text: "Selecciona una opción",
        },
        {
          value: "ENT",
          text: "Entregado",
        },
        {
          value: "CAN",
          text: "Cancelado",
        },
      ],
      loadingFinish: false,
      nameDestinatario: null
    };
  },
  validations: {
    formDelivery: {
      selectedStatus: { required },
      // deliveryObservations: { required }
    }
  },
  watch: {
    searchGuide: function (newValue) {
      if (newValue === "") {
        this.resetInfoSearch()
      }
    },
    startDate: function (newValue) {
      if (this.startDate > this.endDate) {
        this.endDate = this.startDate;
      }
      const startDate = new Date(this.startDate);
      const minDate = new Date(
        startDate.getFullYear(),
        startDate.getMonth(),
        startDate.getDate() + 1
      );
      this.minEndDate = minDate;
    },
    nameDestinatario(newValue){
      if(newValue === ""){
        this.resetInfoSearch()
      }
    }
  },
  methods: {
    resetInfoSearch(){
      this.guidesTable = []
      this.endCursor = null
      this.loadingGuides = true
      this.getGuidesLocal()
    },
    async getGuidesLocal(isSearch = false) {
      if (isSearch === true) {
        this.loadingGuides = true;
        this.guidesTable = [];
        this.endCursor = null;
      }
      this.loadingMore = true;
      try {
        const response = await this.$apollo.query({
          query: LIST_GUIDES_LOCAL,
          variables: {
            status: "DES",
            after: this.endCursor,
            guideNumber: this.searchGuide,
            createdGte:
              this.startDate !== null ? new Date(this.startDate) : null,
            createdLte: this.endDate !== null ? new Date(this.endDate) : null,
            name: this.nameDestinatario
          },
        });
        const { guidesLocal } = response.data;
        if (guidesLocal === null) {
          this.loadingGuides = false;
          this.loadingMore = false;
          return toast("error", "No se pudo recuperar la información");
        }
        const data = guidesLocal.edges;
        this.hasNextPage = guidesLocal.pageInfo.hasNextPage;
        this.endCursor = guidesLocal.pageInfo.endCursor;
        this.formatDataTable(data);
        this.loadingGuides = false;
        this.loadingMore = false;
        return true;
      } catch (error) {
        this.loadingGuides = false;
        this.loadingMore = false;
        return toast("error", "Error al recuperar la información");
      }
    },
    formatDataTable(data) {
      for (let [index, item] of data.entries()) {
        const temp = {
          id: index + 1,
          address: item.node.address,
          products: this.formatProductToString(
            item.node.guidelocalproductthroughSet.edges
          ),
          name: item.node.name,
          guideNumber: item.node.guideNumber,
          phoneNumber: item.node.phoneNumber,
          value: `$${formatPrice(
            this.sumValueGuide(item.node.guidelocalproductthroughSet.edges)
          )}`,
        };
        this.guidesTable.push(temp);
      }
    },
    clearDates() {
      this.startDate = null;
      this.endDate = null;
      this.guidesTable = [];
      this.endCursor = null;
      this.hasNextPage = null;
      this.loadingGuides = true;
      this.getGuidesLocal();
    },
    formatProductToString(data) {
      let text =
        '<ul style="padding: 0; margin-left: 10px; list-style: none;">';
      for (let item of data) {
        text += `<li>${item.node.product.name} X ${item.node.quantity}</li>`;
      }
      text += "</ul>";
      return text;
    },
    sumValueGuide(data) {
      let total = 0;
      for (let item of data) {
        total += item.node.price;
      }
      return total;
    },
    async deliveryAddressGuide() {
      this.loadingFinish = true
      try {
        const response = await this.$apollo.mutate({
          mutation: DELIVERY_ADDRESS_GUIDE,
          variables: {
            guides: [this.guideLocalSelect.guideNumber],
            status: this.formDelivery.selectedStatus,
            deliveryObservations: this.formDelivery.deliveryObservations,
          },
        });
        const { deliverAddressGuide } = response.data;
        if (deliverAddressGuide === null) {
          this.loadingFinish = false
          return toast("error", "No se pudo finalizar, por favor intenta de nuevo");
        }
        if (deliverAddressGuide.success === false) {
          this.loadingFinish = false
          return toast("error", "No se pudo finalizar, por favor intenta de nuevo");
        }
        this.$bvModal.hide("modal-lg");
        this.loadingFinish = false
        this.formDelivery.selectedStatus = null
        this.formDelivery.deliveryObservations = null
        this.loadingGuides = true
        this.endCursor = null
        this.getGuidesLocal()
        return toast("success", "Finalizado con exito");
      } catch (error) {
        this.loadingFinish = false
        return toast("error", "No se pudo finalizar, por favor intenta de nuevo");
      }
    },
    state(item) {
      const { $dirty, $error } = item;
      return $dirty ? !$error : null;
    },
    validate(){
      this.$v.formDelivery.$touch()
      const isValid = !this.$v.formDelivery.$invalid
      if (isValid === true){
        this.deliveryAddressGuide()
      }
      return isValid
    },
    onCloseModal(){
      this.$v.formDelivery.$reset()
      this.formDelivery.selectedStatus = null
      this.formDelivery.deliveryObservations = null
    }
  },
  beforeMount() {
    this.getGuidesLocal();
  },
};
</script>
<style scoped>
.text_right {
  text-align: right;
}
</style>